eather {\r\n    font-family: \"DejaVu Sans Mono\", sans-serif;\r\n    font-style: normal;\r\n    font-weight: 400;\r\n    font-size: 1em;\r\n    align-items: center;\r\n    color: #2b518b;\r\n}\r\n.btn-login {\r\n    background: #7d92b3;\r\n    border-radius: 5px;\r\n    width: 64px;\r\n    height: 32px;\r\n    border: none;\r\n    color: #e5e5e5;\r\n}\r\n.btn-login:hover {\r\n    background: #e5e5e5;\r\n    border-radius: 5px;\r\n    width: 64px;\r\n    height: 32px;\r\n    border: 1px #7d92b3 solid;\r\n    color: #7d92b3;\r\n}\r\n\r\n.main {\r\n    margin-left: 20px;\r\n    grid-column: 1 / 2;\r\n    grid-row: 2 / 3;\r\n    /* background-color: rgba(255, 255, 255, 0.2); */\r\n}\r\n.aside {\r\n    background-color: rgba(255, 255, 255, 0.2);\r\n    margin-right: 20px;\r\n    grid-column: 2 / 3;\r\n    grid-row: 2 / 3;\r\n}\r\n\r\n.main.content {\r\n    display: flex;\r\n    flex-direction: column;\r\n    align-items: center;\r\n    gap: 18px;\r\n    max-width: 100%;\r\n    min-height: 200px;\r\n    background: #e5e5e5;\r\n    border-radius: 10px;\r\n    margin: 0px 58px 40px 8px;\r\n    padding: 26px;\r\n}\r\n.aside-content {\r\n    width: auto;\r\n    height: 100%;\r\n    background-color: rgba(56, 220, 231, 0.1);\r\n}\r\n.row {\r\n    display: grid;\r\n    grid-template-columns: 1fr 1fr;\r\n    grid-template-rows: auto auto;\r\n    justify-items: center;\r\n    flex-wrap: wrap;\r\n    -moz-column-gap: 18px;\r\n         column-gap: 18px;\r\n}\r\n\r\n.btn .arrival {\r\n    display: flex;\r\n    justify-content: center;\r\n    align-items: center;\r\n    grid-column: 1 / 2;\r\n    grid-row: 1 / 2;\r\n}\r\n\r\n.footer {\r\n    background-color: rgb(18, 207, 11);\r\n    /* filter: opacity(0.5); */\r\n    width: calc(100% - 2 * var(--marg2));\r\n    height: 74px;\r\n    background: #e5e5e5;\r\n    border-radius: 10px;\r\n    margin: var(--marg2);\r\n    min-width: 320px;\r\n    grid-column: 1 / span 2;\r\n}\r\n\r\n.btn {\r\n    display: flex;\r\n    justify-content: cen